<template>
  <v-container class="messageRandom">
      <v-row>
          <v-col cols="12" class="text-center">
             <h1>🙂 {{message}} 🙂</h1>
          </v-col>
      </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Home",
  data: () => ({
    message: "",
  }),
  sockets: {
    async connect() {},

    async disconnect() {
      console.log("Esta desconectado");
    },
    messageRandom: async function (data) {
      this.message = data;
    },
  },
  methods: {},
  created() {
    this.$socket.on("messageRandom", (data) => {
      this.message = data;
    });
  },
};
</script>
